import { createTheme, responsiveFontSizes } from "@mui/material";
import { grey } from "@mui/material/colors";

let theme = createTheme({
  palette: {
    primary: {
      main: "#353353",
    },
    secondary: {
      main: "#ff4c60",
    },
    info: {
      main: "#FFD15C",
    },
    text: {
      primary: grey[300],
    },
  },
});

theme = responsiveFontSizes(theme);
export default theme;
